import React, {Component} from 'react';

import headeLogo from './logo.png';

import './header-app.scss';

class HeaderApp extends Component {

    state = {
        links: ['About me', 'Relationships', 'Requirements', 'Users', 'Sign Up'],
        activeItem: null
    }

    changeActive(num) {
        this.setState({
            activeItem: num
        })
    }

    showMobileNav() {
        let nav =  document.getElementById('mobile')

        if (nav.style.left === '0px') {
            nav.style.left = "-291px";
        } else {
            nav.style.left = 0;
        }
    }

    animatedCross() {
        document.querySelector('.nav__burger').classList.toggle('nav__burger_active');
    }

    render() {
        const {links, activeItem} = this.state;

        return (
            <header>
                <div className="header">
                    <div className="header__container">
                        <img src={headeLogo} alt="header-logo" className="header__logo"/>
                        <nav className="header__nav">
                            {
                                links.map((item, index) => {
                                    if (index === activeItem) {
                                        return (
                                            <div className="header__item" key={index} onClick={
                                                () => this.changeActive(index)
                                            }>
                                                <a className="header__link active" href="#register">{item}</a>
                                            </div>
                                        )
                                    } else {
                                        return (
                                            <div className="header__item" key={index} onClick={
                                                () => {
                                                    this.changeActive(index)
                                                }
                                            }>
                                                <a className="header__link" href="#register">{item}</a>
                                            </div>
                                            )
                                    }
                                    
                                })
                            }
                        </nav>
                        <div  id="mobile" className="nav__mobile">
                            <div className="nav__logo">
                                <img src={headeLogo} alt="header-logo" className="header__logo"/>
                            </div>
                            <div className="nav__section">
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">About me</a>
                                </div>
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">Relationships</a>
                                </div>
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">Users</a>
                                </div>
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">Sign Up</a>
                                </div>
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">Terms and Conditions</a>
                                </div>
                            </div>
                            <div className="nav__section">
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">How it works</a>
                                </div>
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">Partnership</a>
                                </div>
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">Help</a>
                                </div>
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">Leave testimonial</a>
                                </div>
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">Contact us</a>
                                </div>
                            </div>
                            <div className="nav__section">
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">Articles</a>
                                </div>
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">Our news</a>
                                </div>
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">Testimonials</a>
                                </div>
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">Licenses</a>
                                </div>
                                <div className="nav__item">
                                    <a href="#register" className="nav__link">Privacy Policy</a>
                                </div>
                            </div>
                        </div>
                        <div className="nav__burger"
                            onClick={() => {
                                this.showMobileNav()
                                this.animatedCross()
                            }}>
                            <span></span>
                            <span></span>
                            <span></span>
                        </div>
                    </div>
                </div>
            </header>
        )
    }

}

export default HeaderApp